import { useState } from 'react';
import {
  Box,
  FormControl,
  FormLabel,
  Input,
  Button,
  Text,
  Tabs,
  TabList,
  TabPanels,
  Tab,
  TabPanel,
} from '@chakra-ui/react';
import { useDispatch } from 'react-redux';
// import { useSelector, useDispatch } from 'react-redux';
import { login } from '../features/user';
import React from 'react';

const LoginRegister = () => {
  // const user = useSelector(state => state.user.value);
  const [tabIndex, setTabIndex] = React.useState(0);
  const dispatch = useDispatch();
  const [input, setInput] = useState({
    email: '',
    password: '',
  });

  const [input2, setInput2] = useState({
    firstname: '',
    lastname: '',
    email: '',
    password: '',
    phone: '',
  });

  const handleInputChangeLogin = name => e => {
    setInput({ ...input, [name]: e.target.value });
  };

  const handleInputChangeRegister = name => e => {
    setInput2({ ...input2, [name]: e.target.value });
  };

  const requestLogin = e => {
    e.preventDefault();

    let formData = new FormData();
    formData.append('email', input.email);
    formData.append('password', input.password);

    fetch('/api/login', {
      method: 'POST',
      body: formData,
    }).then(res => {
      res.json().then(res2 => {
        dispatch(
          login({
            id: res2.user.id,
            email: res2.user.Email,
            token: res2.token,
            status: res2.status,
            name: res2.user.FirstName,
          })
        );
      });
    });
  };

  const requestRegister = e => {
    e.preventDefault();

    let formData = new FormData();
    formData.append('firstname', input2.firstname);
    formData.append('lastname', input2.lastname);
    formData.append('email', input2.email);
    formData.append('password', input2.password);
    formData.append('phone', input2.phone);

    fetch('/api/register', {
      method: 'POST',
      body: formData,
    }).then(res => {
      res.json().then(res2 => {
        setTabIndex(0);
      });
    });
  };

  const handleTabsChange = index => {
    setTabIndex(index);
  };

  return (
    <Box p={5} boxShadow={'xl'} rounded={'md'}>
      <Tabs index={tabIndex} onChange={handleTabsChange}>
        <TabList>
          <Tab>Login</Tab>
          <Tab>Register</Tab>
        </TabList>
        <TabPanels>
          <TabPanel>
            <Text fontSize={'5xl'}>Login</Text>
            <form onSubmit={requestLogin}>
              <FormControl>
                <FormLabel htmlFor="email">Email</FormLabel>
                <Input
                  id="email"
                  type="email"
                  value={input.email}
                  onChange={handleInputChangeLogin('email')}
                />
              </FormControl>
              <FormControl>
                <FormLabel htmlFor="password">Password</FormLabel>
                <Input
                  id="password"
                  type="password"
                  value={input.password}
                  onChange={handleInputChangeLogin('password')}
                />
              </FormControl>

              <Button mt={3} width="100%" colorScheme="teal" type="submit">
                Submit
              </Button>
            </form>
          </TabPanel>
          <TabPanel>
            <Text fontSize={'5xl'}>Register</Text>
            <form onSubmit={requestRegister}>
              <FormControl>
                <FormLabel>First Name</FormLabel>
                <Input
                  id="firstName"
                  value={input2.firstname}
                  onChange={handleInputChangeRegister('firstname')}
                />
              </FormControl>

              <FormControl>
                <FormLabel>Last Name</FormLabel>
                <Input
                  id="lastName"
                  value={input2.lastname}
                  onChange={handleInputChangeRegister('lastname')}
                />
              </FormControl>

              <FormControl>
                <FormLabel>Email</FormLabel>
                <Input
                  id="email2"
                  value={input2.email}
                  onChange={handleInputChangeRegister('email')}
                />
              </FormControl>

              <FormControl>
                <FormLabel>Phone</FormLabel>
                <Input
                  id="phone"
                  value={input2.phone}
                  onChange={handleInputChangeRegister('phone')}
                />
              </FormControl>

              <FormControl>
                <FormLabel htmlFor="password">Password</FormLabel>
                <Input
                  id="password2"
                  type="password"
                  value={input2.password}
                  onChange={handleInputChangeRegister('password')}
                />
              </FormControl>

              <Button mt={3} width="100%" colorScheme="teal" type="submit">
                Submit
              </Button>
            </form>
          </TabPanel>
        </TabPanels>
      </Tabs>
    </Box>
  );
};

export default LoginRegister;
