import { useSelector, useDispatch } from 'react-redux';
import {
  Text,
  Box,
  Button,
  Flex,
  Spacer,
  Center,
  Stack,
} from '@chakra-ui/react';
import { modStatus } from '../features/user';
import { useState, useEffect } from 'react';
import Card from './subcomponents/Card';
import moment from 'moment';
import { RepeatIcon } from '@chakra-ui/icons';

const Home = () => {
  const user = useSelector(state => state.user.value);
  const [userState, setuserState] = useState({
    name: user.name,
    email: user.email,
    status: user.status,
  });
  const [userStat, setuserStat] = useState({
    late: 0,
    earyleave: 0,
    overtime: 0,
  });

  const [userHistory, setuserHistory] = useState([]);
  const dispatch = useDispatch();
  let button;

  let formData = new FormData();
  formData.append('userid', user.id);

  const requestClockIn = () => {
    fetch('/api/attendance/checkin', {
      method: 'POST',
      body: formData,
    }).then(res => {
      res.json().then(res2 => {
        dispatch(modStatus('in'));
        setuserState({ ...userState, status: 'in' });
      });
    });
  };

  const requestClockOut = () => {
    fetch('/api/attendance/checkout', {
      method: 'PUT',
      body: formData,
    }).then(res => {
      res.json().then(res2 => {
        dispatch(modStatus('out'));
        setuserState({ ...userState, status: 'out' });
      });
    });
  };

  if (userState.status == 'in') {
    button = (
      <Button mt={3} width="100%" colorScheme="red" onClick={requestClockOut}>
        Clock Out
      </Button>
    );
  } else {
    button = (
      <Button mt={3} width="100%" colorScheme="teal" onClick={requestClockIn}>
        Clock In
      </Button>
    );
  }
  const [toggleRefresh, setToggleRefresh] = useState(true);
  const handleRefreshToggle = () => {
    setToggleRefresh(prev => !prev);
  };

  useEffect(() => {
    fetch('/api/attendances/summary', {
      method: 'POST',
      body: formData,
    }).then(res => {
      res.json().then(res2 => {
        setuserStat({
          ...userStat,
          late: res2.late,
          overtime: res2.overtime,
          earyleave: res2.early,
        });
      });
    });

    fetch('/api/attendances', {
      method: 'POST',
      body: formData,
    }).then(res => {
      res.json().then(res2 => {
        setuserHistory(res2.data);
        console.log(res2);
      });
    });
  }, [toggleRefresh]);

  const convertTimeDate = time => {
    let t = new Date(time);
    let format = moment(t).format('Do MMMM yyy');
    return format;
  };

  const convertTime = time => {
    let t = new Date(time);
    let format = moment(t).format('HH:MM');
    return format;
  };

  return (
    <div>
      <Flex>
        <Spacer />
        <Button onClick={handleRefreshToggle}>
          <RepeatIcon />
        </Button>
      </Flex>
      <Box p={5} boxShadow={'lg'} rounded={'md'} borderWidth="2px">
        <Text fontSize={'6xl'}>Welcome {user.name} !</Text>
        <Text>Current status: {userState.status}</Text>
        {button}
      </Box>
      <Flex>
        <Card>
          <Flex>
            <Text fontSize={'2xl'}>Late</Text>
            <Spacer />
            <Text fontSize={'lg'}>{userStat.late + 'm'}</Text>
          </Flex>
        </Card>
        <Spacer />

        <Card>
          <Flex>
            <Text fontSize={'2xl'}>Early leave</Text>
            <Spacer />
            <Text fontSize={'lg'}>{userStat.earyleave + 'm'}</Text>
          </Flex>
        </Card>

        <Spacer />
        <Card>
          <Flex>
            <Text fontSize={'2xl'}>Overtime</Text>
            <Spacer />
            <Text fontSize={'lg'}>{userStat.overtime + 'm'}</Text>
          </Flex>
        </Card>
      </Flex>

      {userHistory.length > 0 &&
        userHistory.map((item, index) => {
          let date = convertTimeDate(item.ClockIn);
          let clockedin = convertTime(item.ClockIn);
          let clockedout = convertTime(item.ClockOut);
          return (
            <Card key={index}>
              <Flex>
                <Text fontSize={'2xl'}>{date}</Text>
                <Spacer />
                <Text fontSize={'lg'} mr={'2rem'}>
                  Clocked in: {clockedin}
                </Text>
                <Text fontSize={'lg'}>Clocked out: {clockedout}</Text>
              </Flex>
            </Card>
          );
        })}
    </div>
  );
};

export default Home;
